import Vue from 'vue'
import VueRouter from 'vue-router'

import Login from '../views/Login'
import Admin from '../views/Admin'

import Index from '../components/admin/Index'
import AddArt from '../components/article/AddArt'
import ArtList from '../components/article/ArtList'
import CateList from '../components/category/CateList'
import UserList from '../components/user/UserList'
import Profile from '../components/user/Profile'

Vue.use(VueRouter)

const routes = [
  {
    path: '/login',
    name: 'login',
    meta: {
      title: '登录',
    },
    component: Login,
  },
  {
    path: '/',
    name: 'admin',
    meta: {
      title: 'GinBlog 后台管理',
    },
    component: Admin,
    children: [
      {
        path: 'index',
        component: Index,
        meta: {
          title: 'GinBlog 后台管理',
        },
      },
      {
        path: 'addart',
        component: AddArt,
        meta: {
          title: '添加文章',
        },
      },
      {
        path: 'addart/:id',
        component: AddArt,
        meta: {
          title: '编辑文章',
        },
        props: true,
      },
      {
        path: 'artlist',
        component: ArtList,
        meta: { title: '文章列表' },
      },
      {
        path: 'catelist',
        component: CateList,
        meta: { title: '分类列表' },
      },
      {
        path: 'userlist',
        component: UserList,
        meta: { title: '用户列表' },
      },
      {
        path: 'profile',
        component: Profile,
        meta: { title: '个人设置' },
      },
    ],
  },
]

const router = new VueRouter({
  routes,
})

