<template>
  <div>
    <a-card>
      <a-row :gutter="20">
        <a-col :span="4">
          <a-button type="primary" @click="addCateVisible = true"
            >新增分类</a-button
          >
        </a-col>
      </a-row>
      <a-table
        rowKey="id"
        :columns="columns"
        :pagination="pagination"
        :dataSource="CateList"
        bordered
        @change="handleTableChange"
      >
        <template slot="action" slot-scope="data">
          <div class="actionSlot">
            <a-button
              type="primary"
              icon="edit"
              style="margin-right: 15px"
              @click="editCate(data.id)"
              >编辑</a-button
            >
            <a-button
              type="danger"
              icon="delete"
              style="margin-right: 15px"
              @click="deleteCate(data.id)"
              >删除</a-button
            >
          </div>
        </template>
      </a-table>
    </a-card>
    <a-modal
      closable
      title="新增分类"
      :visible="addCateVisible"
      width="60%"
      @ok="addCateOk"
      @cancel="addCateCancel"
      destroyOnClose
    >
      <a-form-model :model="newCate" :rules="addCateRules" ref="addCateRef">
        <a-form-model-item label="分类名称" prop="name">
          <a-input v-model="newCate.name" />
        </a-form-model-item>
      </a-form-model>
    </a-modal>
    <a-modal
      closable
      title="编辑分类"
      :visible="editCateVisible"
      width="60%"
      @ok="editCateOk"
      @cancel="editCateCancel"
    >
      <a-form-model :model="CateInfo" :rules="CateRules" ref="addCateRef">
        <a-form-model-item label="分类名称" prop="name">
          <a-input v-model="CateInfo.name" />
        </a-form-model-item>
      </a-form-model>
    </a-modal>
  </div>
</template>

<script>
const columns = [
  {
    title: 'ID',
    dataIndex: 'id',
    width: '10%',
    key: 'id',
    align: 'center',
  },
  {
    title: '分类名',
    dataIndex: 'name',
    width: '20%',
    key: 'name',
    align: 'center',
  },
  {
    title: '操作',
    width: '30%',
    key: 'action',
    align: 'center',
    scopedSlots: { customRender: 'action' },
  },
]

export default {
  name: 'CateList',
  data() {
    return {
      pagination: {
        pageSizeOptions: ['5', '10', '20'],
        pageSize: 5,
        total: 0,
        showSizeChanger: true,
        showTotal: (total) => `共${total}条`,
      },
      CateList: [],
      columns,
      queryParam: {
        pageSize: 5,
        pageNum: 1,
      },
      CateInfo: {
        name: '',
        id: 0,
      },
      newCate: {
        name: '',
      },
      editVisible: false,
      addCateVisible: false,
      editCateVisible: false,
      CateRules: {
        name: [
          {
            validator: (role, value, callback) => {
              if (this.CateInfo.name === '') {
                callback(new Error('请输入分类名'))
              } else {
                callback()
              }
            },
            trigger: 'blur',
          },
        ],
      },
      addCateRules: {
        name: [
          {
            validator: (role, value, callback) => {
              if (this.newCate.name === '') {
                callback(new Error('请输入分类名'))
              } else {
                callback()
              }
            },
            trigger: 'blur',
          },
        ],
      },
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    async getCateList() {
      const { data: res } = await this.$http.get('admin/category', {
        pagesize: this.queryParam.pageSize,
        pagenum: this.queryParam.pageNum,
      })
      if (res.status !== 200) return this.$message.error(res.message)
      this.CateList = res.data
      this.pagination.total = res.total
    },
    handleTableChange(pagination, _filters, _sorter) {
      var pager = { ...this.pagination }
      pager.current = pagination.current
      pager.pageSize = pagination.pageSize
      this.queryParam.pageSize = pagination.pageSize
      this.queryParam.pageNum = pagination.current
      if (pagination.pageSize !== this.pagination.pageSize) {
        this.queryParam.pageNum = 1
        pager.current = 1
      }
      this.pagination = pager
      this.getCateList()
    },
    deleteCate(id) {
      this.$confirm({
        title: '提示',
        content: '此操作将永久删除该分类, 是否继续?',
        onOk: async () => {
          const { data: res } = await this.$http.delete(`category/${id}`)
          if (res.status !== 200) return this.$message.error(res.message)
          this.$message.success('删除成功')
          this.getCateList()
        },
        onCancel: () => {
          this.$message.info('已取消删除')
        },
      })
    },
    addCateOk() {
      this.$refs.addCateRef.validate(async (valid) => {
        if (!valid) return this.$message.error('参数不符合,请填写完整信息')
        const { data: res } = await this.$http.post('category/add', {
          name: this.newCate.name,
        })
        if (res.status !== 200) return this.$message.error(res.message)
        this.$refs.addCateRef.resetFields()
        this.addCateVisible = false
        this.$message.success('添加分类成功')
        await this.getCateList()
      })
    },
    addCateCancel() {
      this.$refs.addCateRef.resetFields()
      this.addCateVisible = false
      this.$message.info('新增分类已取消')
    },
    async editCate(id) {
      this.editCateVisible = true
      const { data: res } = await this.$http.get(`category/${id}`)
      this.CateInfo = res.data
      this.CateInfo.id = id
    },
    editCateOk() {
      this.$refs.addCateRef.validate(async (valid) => {
        if (!valid) return this.$message.error('参数不符合,请填写完整信息')
        const { data: res } = await this.$http.put(
          `category/${this.CateInfo.id}`,
          {
            name: this.CateInfo.name,
          }
        )
        if (res.status !== 200) return this.$message.error(res.message)
        this.editCateVisible = false
        this.$message.success('更新分类信息成功')
        this.getCateList()
      })
    },
    editCateCancel() {
      this.$refs.addCateRef.resetFields()
      this.editCateVisible = false
      this.$message.info('编辑已取消')
    },
  },
}
</script>

<style scoped>
.actionSlot {
  display: flex;
  justify-content: center;
}
</style>
